.toggle-voice-video .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #E1AD01;
  -webkit-transition: .4s;
  transition: .4s;
   border-radius: 34px;
}
.toggle-voice-video .slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 50%;
}
.toggle-voice-video input:checked + .slider {
  background-color: #2ab934;
}
.toggle-voice-video input:focus + .slider {
  -webkit-box-shadow: 0 0 1px #2196F3;
          box-shadow: 0 0 1px #2196F3;
}
.toggle-voice-video input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  transform: translateX(55px);
}
/*------ ADDED CSS ---------*/
.toggle-voice-video .slider:after
{
 content:'VOICE';
 color: white;
 display: block;
 position: absolute;
 -webkit-transform: translate(-50%,-50%);
         transform: translate(-50%,-50%);
 top: 50%;
 left: 57%;
 font-size: 10px;
 font-family: Verdana, sans-serif;
}
.toggle-voice-video input:checked + .slider:after
{  
  content:'VIDEO';
}
